@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,800&display=swap);
html,
body,
#root {
  height: 100%;
}

input:active,
input:focus,
textarea:active,
textarea:focus,
button:active,
button:focus {
  outline: none;
}

a:hover {
  text-decoration: none !important;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  font-family: "Nunito Sans", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
*::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media only screen and (max-width: 600px) {
  .Grid::-webkit-scrollbar {
    width: 0px;
  }
}

@font-face {
  font-family: 'icomoon';
  src:  url(/pokedex-ts/static/media/icomoon.b25d3830.eot);
  src:  url(/pokedex-ts/static/media/icomoon.b25d3830.eot#iefix) format('embedded-opentype'),
    url(/pokedex-ts/static/media/icomoon.94967134.ttf) format('truetype'),
    url(/pokedex-ts/static/media/icomoon.7372a6af.woff) format('woff'),
    url(/pokedex-ts/static/media/icomoon.279f0eb7.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

